.popular {
  padding-top: 140px;

  &__wrapper {
    display: grid;
    grid-template-columns: 180px 1fr;
    gap: 18px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__filter {
    @media (max-width: 992px) {
      display: none;
    }
    .list {
      display: flex;
      flex-direction: column;
      gap: 10px;
      li {
        .subitems {
          margin-top: 10px;
          display: none;
          ul {
            display: flex;
            flex-direction: column;
            gap: 10px;

            .btn {
              color: var(--gray);
              background: #fdffdd;

              @media (any-hover: hover) {
                &:hover {
                  background: #f7ff80;
                }
              }
            }
          }
        }

        @media (any-hover: hover) {
          &:hover {
            .subitems {
              display: block;
            }
          }
        }
      }
      .btn {
        border-radius: 10px;
        padding: 10px 13px 10px 13px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        background: var(--blue);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        width: 100%;
        text-align: left;

        @media (any-hover: hover) {
          &:hover {
            color: var(--white);
            background: var(--hovered);
            svg {
              rotate: 180deg;
            }
            svg path {
              stroke: var(--white);
            }
          }
        }
      }
    }
  }

  &__banner {
    margin-bottom: 60px;
  }

  // ----------

  &__inner {
    border-radius: 20px;
    background: #f8f8f8;
    padding: 30px 40px;
    position: relative;

    @media (max-width: 767px) {
      padding: 20px 10px;
    }
  }

  &__image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
    max-width: 320px;
    img {
      object-fit: contain;
    }
    @media (max-width: 1140px) {
      display: none;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    margin-bottom: 30px;
    margin-top: 40px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 136%;
    letter-spacing: -0.02em;
    color: var(--gray);
    margin: 0;
    margin-bottom: 28px;
  }

  &__tags {
    display: flex;
    gap: 15px;

    .tag {
      border-radius: 100px;
      padding: 10px 22px;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      display: flex;
      align-items: center;
      gap: 6px;
      &.active {
        background: var(--blue);
        color: var(--accent);
      }
    }
  }

  .catalog-offer__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
  }
}
